import React from "react";
import ListaCitasPaciente from "../../Components/Paciente/ListaCitasPaciente";
import PacienteEncabezado from "../../Components/Paciente/PacienteEncabezado";


export default function CitasPaciente(){
    const nombre = localStorage.getItem('nombre');
    return(
        <div className="fondoSistema">
            <div className="cuadro">
                <PacienteEncabezado/>
                <div className="navigation">
                    <ul className="ulNavegacion">
                        <li className="liNavegacion">
                            <a className="aNavegacion" href="/iniciopaciente"><i className="fa fa-home"></i><br/>Inicio</a>
                        </li>
                        <li>
                            <a className="menuClickeado aNavegacion" href="/citaspaciente"><i className="fa fa-calendar-check-o"></i><br/>Citas</a>
                        </li>
                        <li>
                            <a className="aNavegacion" href="/historialmedicopaciente"><i className="fa fa-paste"></i><br/>Historial Mdico</a>
                        </li>
                    </ul>
                </div>
                <div className="contenido">
                    <h1>{nombre} sus citas son:</h1>
                </div>
                <div>
                    <ListaCitasPaciente/>
                </div>
            </div>
        </div>
    );
}